<template>
  <div class="container">
    <div class="headerWrap f-c j-b">
      <!--? 未登录状态-->
      <van-button class="no-login-btn" type="default" @click="$router.push('/login')" v-if="!userToken">
        <div class="no-login f-c">
          <img src="~@/assets/img/mobile.png" alt="">
          <span>登录/注册</span>
        </div>
      </van-button>
      <!--? 已登录状态      -->
      <div style="width: 100%" v-else>
        <div class="userTop f-r j-b">
          <div class="f-r">
            <van-image
              fit="cover"
              :src="userBaseInfo.photo"
              class="avatar"
            />
            <span class="user-name">{{ userBaseInfo.name }}</span>
          </div>
          <van-button type="default" class="edit-info" size="mini" :to="{path:'/edit'}">编辑资料</van-button>
        </div>
        <div class="userFollow f-r">
          <div class="follow-item">
            <p>{{ userBaseInfo.art_count }}</p>
            <p>头条</p>
          </div>
          <div class="follow-item">
            <p>{{ userBaseInfo.follow_count }}</p>
            <p>关注</p>
          </div>
          <div class="follow-item">
            <p>{{ userBaseInfo.fans_count }}</p>
            <p>粉丝</p>
          </div>
          <div class="follow-item">
            <p>{{ userBaseInfo.like_count }}</p>
            <p>获赞</p>
          </div>
        </div>
      </div>
    </div>
    <van-grid clickable :column-num="2" class="saveAndHistory ">
      <van-grid-item>
        <template #icon>
          <i class="iconfont icon-shoucang"></i>
        </template>
        <template #text>
          <span class="text">收藏</span>
        </template>
      </van-grid-item>
      <van-grid-item>
        <template #icon>
          <i class="iconfont icon-lishi"></i>
        </template>
        <template #text>
          <span class="text">历史</span>
        </template>
      </van-grid-item>
    </van-grid>

    <van-cell title="消息通知" class="m-t-10" is-link/>
    <van-cell title="小智同学" is-link/>
    <van-cell title="退出登录" v-if="userToken" clickable @click="logout" class="m-t-10"
              style="text-align: center;color: #d86262"/>
  </div>
</template>

<script>
import {mapState} from 'vuex';
import getInfo from "../../api/user/getInfo";

export default {
  name: "My",
  components: {},
  props: [],
  data() {
    return {
      userBaseInfo: {}
    }
  },
  methods: {
    logout() {
      this.$dialog.confirm({
        title: '退出登录',
        message: '确定要退出登录嘛？',
      })
        .then(() => {
          // on confirm
          this.$store.commit('setUserToken', null)
        })
        .catch(() => {
          // on cancel
        });
    },
    async getUserInfo() {

      try {
        const {data: {data}} = await getInfo();
        this.userBaseInfo = data;
        // console.log(data)
      } catch (e) {

      }
    }

  },
  computed: {
    ...mapState(['userToken'])
  },
  watch: {},
  created() {
    this.getUserInfo()
  },
  mounted() {
  }
}
</script>

<style scoped lang="less">
.container {
  .headerWrap {
    height: 360px;
    box-sizing: border-box;
    background: url("~@/assets/img/banner.png");
    background-size: cover;
    //outline: 1px solid rgb(35.0, 222.0, 35.0);
    padding: 80px 25px 0 25px;

    .userTop {
      width: 100%;

      .avatar {
        width: 132px;
        height: 132px;
        border-radius: 50%;
        border: 5px solid white;
        overflow: hidden;
      }

      // ? 用户名
      .user-name {
        font-size: 30px;
        color: #fff;
        margin-left: 15px;
      }

      .van-button__text {
        font-size: 20px;
        color: #666;
        line-height: 20px;
        //text-align: center;
      }

      .edit-info {
        border-radius: 15px;
        padding: 0 20px;
      }

    }

    .userFollow {
      width: 100%;

      .follow-item {
        flex: 1;
        color: #fff;
        text-align: center;
        line-height: 10px;
        //height: 50%;

        p {
          //? 数量
          &:first-child {
            font-size: 36px;
          }

          //? 标题
          &:last-child {
            font-size: 23px;
          }
        }

      }
    }

    /deep/ .no-login-btn {
      background: transparent;
      border: none;
      height: auto;
    }

    .no-login {

      img {
        width: 132px;
        height: 132px;
      }

      span {
        margin-top: 15px;
        font-size: 28px;
        color: #fff;
      }
    }
  }

  .saveAndHistory {
    //height: 140px;
    .iconfont {
      font-size: 45px;

      &:first-child {
        color: #eb5253;
      }

      &:last-child {
        color: #ff9d1d;
      }
    }

    .text {
      font-size: 28px;
    }
  }


}

</style>
